import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Sources from '../views/Sources'
import Act1 from '../views/Acts/Act1'
import Act2 from '../views/Acts/Act2'
import Act3 from '../views/Acts/Act3'
import Act4 from '../views/Acts/Act4'
import Act5 from '../views/Acts/Act5'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/sources',
    name: 'sources',
    component: Sources
  },
  {
    path: '/act1',
    name: 'act1',
    component: Act1
  },
  {
    path: '/act2',
    name: 'act2',
    component: Act2
  },
  {
    path: '/act3',
    name: 'act3',
    component: Act3
  },
  {
    path: '/act4',
    name: 'act4',
    component: Act4
  },
  {
    path: '/act5',
    name: 'act5',
    component: Act5
  }
]

const router = new VueRouter({
  routes
})

export default router
