<template>
    <v-app>

        <v-tabs
                background-color="grey lighten-2"
                center-active
                fixed-tabs
        >
            <v-tab>
                Summaries
            </v-tab>

            <v-tab-item>
                <v-expansion-panels
                        multiple
                        popout
                >
                    <v-expansion-panel :key="panel.text" v-for="panel in panels">
                        <v-expansion-panel-header>
                            <h3 class="text-center">Scene {{panel.number}}</h3>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-card>
                                <v-card-text>
                                    {{panel.text}}
                                </v-card-text>
                            </v-card>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-tab-item>

            <v-tab>
                Themes
            </v-tab>

            <v-tab-item>
                <v-card
                        :key="themecard.name"
                        class="mx-auto"
                        shaped
                        v-for="themecard in themecards"
                >
                    <v-card-title>{{themecard.name}}</v-card-title>
                    <v-expansion-panels
                            multiple
                            popout
                    >
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                Quote
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{themecard.quote}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                        <v-expansion-panel >
                            <v-expansion-panel-header>
                                Explanation
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{themecard.definition}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-card>
            </v-tab-item>

            <v-tab>
                Quotes
            </v-tab>

            <v-tab-item>
                <v-card
                        :key="quotecard.quote"
                        class="mx-auto"
                        shaped
                        v-for="quotecard in quotecards"
                >
                    <v-card-title>
                        {{quotecard.quote}}
                    </v-card-title>
                    <v-card-subtitle>
                        Location: {{quotecard.location}}
                    </v-card-subtitle>
                    <v-card-text>
                        Character: {{quotecard.character}}
                    </v-card-text>
                    <v-expansion-panels
                            multiple
                            popout
                    >
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                Explanation
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{quotecard.explanation}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-card>
            </v-tab-item>
        </v-tabs>
    </v-app>
</template>

<script>
    export default {
        name: "Act4",
        data: () => ({
            panels: [
                {number: '1', text: 'In a cavern, the weird sisters throw awful ingredients such as "eye of newt and toe of frog" (4.1.14) into a cauldron full of a boiling brew. Hecate arrives, and all dance and sing. One witch cries out "Something wicked this way comes" (4.1.62): Macbeth enters. He commands the witches to answer his questions. The witches conjure up three apparitions. First, a floating head appears and tells Macbeth to beware Macduff. Next, a bloody child appears. The child says that "no man of woman born / Shall harm Macbeth" (4.1.95-96). Finally, a child wearing a crown and holding a tree appears. It says that Macbeth will not be defeated until Great Birnam Wood marches to Dunsinane Hill. Macbeth is pleased: since forests don\'t march, he must be invincible! Macbeth wants to know one more thing: will Banquo\'s heirs have the throne? The witches perform a final conjuring. Eight kings appear walking in a line, the eighth holding a mirror, and all of them followed by Banquo\'s ghost. Macbeth, furious at this sign that Banquo\'s heirs will get the throne, demands answers. But Hecate mocks him and the witches vanish. Lennox enters. He brings word that Macduff has fled to England. In an aside, Macbeth scolds himself for failing to kill Macduff when he wanted to earlier. He vows in the future to act on every impulse, and decides to attack Macduff\'s castle and kill anyone connected to him: servants, wife, and children. '},
                {number: '2', text: 'At Fife (Macduff\'s castle), Lady Macduff is angry. She demands to know why Macduff has gone to England, leaving her behind. She thinks Macduff is a coward. Ross says Macduff\'s flight could be the result of wisdom, not fear. After Ross leaves, Lady Macduff turns to her son. She tells the boy that his father is dead. The boy doesn\'t believe her, but asks if his father is a traitor. Lady Macduff says yes, Macduff is a traitor: a man who swore an oath and broke it and now must hang. The boy thinks if traitors allow themselves to be hanged they must be fools, since there are undoubtedly more traitors than honest men in the world. A servant bursts in to warn of coming danger, then rushes out. Before Lady Macduff or her children can run, murderers enter the chamber, stab Macduff\'s son, and chase Lady Macduff offstage. '},
                {number: '3', text: 'In England, near the palace of King Edward, Macduff urges Malcolm to quickly raise an army against Macbeth. But Malcolm says Macduff might actually be working for Macbeth, a suspicion heightened by the fact that Macduff left his family behind and unprotected in Scotland. Malcolm then adds that he delays attacking Macbeth because he fears that he himself would perhaps be even a worse ruler. Malcolm describes himself as so lustful, vicious, and greedy that he makes Macbeth look kind. Macduff cries out in horror, and says he will leave Scotland forever since there is no man fit to rule it. Malcolm then reveals that none of his self-description was true: it was a trick to test Macduff\'s loyalty. Malcolm now believes that Macduff is loyal to Scotland and not Macbeth, and that he has an army of ten thousand men commanded by the English Lord Siward, ready to invade Scotland. Just then an English doctor enters. Malcolm speaks with the doctor, then tells Macduff that King Edward of England is so saintly that he can cure disease. Ross enters. He tells Malcolm that if he invaded the Scottish people would line up to join his army against Macbeth. Finally, Ross tells Macduff his family has been murdered. Macduff cries out in anguish. Malcolm tells him to fight it like a man. Macduff responds that he must also "feel it like a man" (4.3.223). But they agree that Macduff\'s anger and grief should be used to fuel his revenge. '}
            ],
            themecards: [
                {
                    name: 'Children',
                    definition: 'Macduff speaks these tragic lines upon learning that his wife and children have been murdered. The affectionate imagery he uses to describe them shows how even though he is a strong and powerful warrior, he is also a loving husband and father. The line conveys the sense of overwhelming loss he feels when he hears that he has lost his entire family at the same time. The sense of immense grief sets the stage for his desire to avenge them by eventually killing Macbeth.',
                    quote: 'What, all my pretty chickens and their dam / ' +
                        'At one fell swoop (4.3)'},
                {
                    name: 'Patriotism',
                    definition: 'Macduff speaks this line in his conversation with Malcolm when he becomes very distressed. Because of the lies Malcolm has just told him about his character, Macduff believes that no one is suitable to rule Scotland. At the same time, he still maintains the belief that Macbeth is a terrible ruler, since he obtained power through violent and illegitimate means. Macduff reveals his sincere love for his country through his desire to see a good and honorable man as its king.',
                    quote: 'O nation miserable /' +
                        'With an untitled tyrant, bloody-sceptered (4.3)'},
            ],
            quotecards: [
                //in quote remember about <br/>
                {
                    quote: 'Be bloody, bold, and resolute. Laugh to scorn the power of man, for none of woman born shall harm Macbeth.',
                    location: 'Act IV, Scene I',
                    character: 'Second Apparition',
                    explanation: 'The bloody child is the second of the three apparitions that the witches conjure for Macbeth and it is perhaps the most famous. It tells Macbeth to be confident and without fear, for no person who is born of a woman can harm him. This convinces Macbeth that he is invincible as he would never be killed by another man. He chooses to neglect the first apparition which warned him of Macduff and doesnt realize that the bloody child is in fact the infant Macduff covered with the blood of the untimely ripped womb of his mother.'},
                {
                    quote: 'By the pricking of my thumbs, / ' +
                        'Something wicked this way comes.',
                    location: 'Act 4, Scene 1',
                    character: 'Second Witch',
                    explanation: 'In Act 4, scene 1, Macbeth returns to ask the Witches for more prophesies. Sensing his presence, the Second Witch calls Macbeth wicked. Her words mark the first time the witches describe Macbeth as evil in the play.'},
                {
                    quote: 'Bleed, bleed, poor country! / ' +
                        'Great tyranny, lay thou thy basis sure, / ' +
                        'For goodness dare not check thee.',
                    location: 'Act 4, Scene 3',
                    character: 'Macduff',
                    explanation: 'Macduff says these lines in Act 4 scene 3, after having abandoned his wife and children and fled for his life. Beyond the danger Macbeth poses to Macduff personally, Macduff worries about what effect Macbeths tyranny will have on Scotland. Here, Macduff is angry at himself and others who will not stand up to Macbeth. He will soon learn of Macbeths murder of his entire family, and resolves to help lead the revolt against him.'},
            ]
        })
    }
</script>

