<template>
    <v-app>

        <v-tabs
                background-color="grey lighten-2"
                center-active
                fixed-tabs
        >
            <v-tab>
                Summaries
            </v-tab>

            <v-tab-item>
                <v-expansion-panels
                        multiple
                        popout
                >
                    <v-expansion-panel :key="panel.text" v-for="panel in panels">
                        <v-expansion-panel-header>
                            <h3 class="text-center">Scene {{panel.number}}</h3>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-card>
                                <v-card-text>
                                    {{panel.text}}
                                </v-card-text>
                            </v-card>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-tab-item>

            <v-tab>
                Themes
            </v-tab>

            <v-tab-item>
                <v-card
                        :key="themecard.name"
                        class="mx-auto"
                        shaped
                        v-for="themecard in themecards"
                >
                    <v-card-title>{{themecard.name}}</v-card-title>
                    <v-expansion-panels
                            multiple
                            popout
                    >
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                Quote
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{themecard.quote}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                        <v-expansion-panel >
                            <v-expansion-panel-header>
                                Explanation
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{themecard.definition}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-card>
            </v-tab-item>

            <v-tab>
                Quotes
            </v-tab>

            <v-tab-item>
                <v-card
                        :key="quotecard.quote"
                        class="mx-auto"
                        shaped
                        v-for="quotecard in quotecards"
                >
                    <v-card-title>
                        {{quotecard.quote}}
                    </v-card-title>
                    <v-card-subtitle>
                        Location: {{quotecard.location}}
                    </v-card-subtitle>
                    <v-card-text>
                        Character: {{quotecard.character}}
                    </v-card-text>
                    <v-expansion-panels
                            multiple
                            popout
                    >
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                Explanation
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{quotecard.explanation}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-card>
            </v-tab-item>
        </v-tabs>
    </v-app>
</template>

<script>
    export default {
        name: "Act2",
        data: () => ({
            panels: [
                {number: '1', text: 'It is after midnight in Inverness. Banquo talks with his son Fleance and notices the stars aren\'t shining. He prays for angels to "restrain in me the cursed thoughts that nature gives way to in repose" (2.1.7-8). Macbeth enters. Banquo tells Macbeth his sleep has been troubled by dreams of the weird sisters. Macbeth claims never to think about them. But he suggests they talk about the witches soon, and adds that if Banquo supports him when the time comes he\'ll reward and honor Banquo for it. Banquo says he\'ll be receptive to what Macbeth has to say provided he loses no honor in seeking to gain more. Banquo and Fleance head off to bed. Alone, Macbeth sees a bloody dagger floating in the air. He can\'t grasp it, and can\'t decide whether it\'s a phantom or his imagination. "Nature seems dead" to him (2.1.50). Offstage, Lady Macbeth rings the bell to signal that Duncan\'s attendants are asleep. Macbeth goes to murder Duncan. '},
                {number: '2', text: 'Lady Macbeth waits in agitation for Macbeth to do the deed. She comments that had the sleeping Duncan not looked like her father she\'d have killed him herself. Macbeth enters. He\'s killed Duncan and Duncan\'s attendants. His hands are bloodstained and he\'s upset that when one of the attendants said "God bless us" in his sleep, he was unable to say "Amen." He also thought he heard a voice say "Macbeth does murder sleep" (2.2.34). Lady Macbeth soothes him and tells him to wash his hands, but notices he\'s still carrying the daggers he used to kill Duncan. Macbeth refuses to return to the scene of the crime. Lady Macbeth, furious, runs off to plant the daggers on the attendants. A knock sounds, terrifying Macbeth. He worries that not all the water in the world could wash the blood from his hands. Lady Macbeth returns, her hands now as bloody as Macbeth\'s. But she\'s calm, and identifies the \'mysterious\' knocking as someone at the south entrance. She says: "a little water clears us of this deed" (2.2.65), and tells Macbeth to go and put his nightgown on so no one will suspect them. Macbeth wishes that the knocking could wake Duncan. '},
                {number: '3', text: 'A porter goes to the answer the door, joking to himself that he is the doorkeeper at the mouth of hell, and mocking whoever might be knocking to get into hell. At the door are Macduff and Lennox. Macduff good-naturedly asks what took so long. The porter blames drunkenness, and makes a series of jokes about alcohol and its effects on men. Macbeth enters, pretending to have just woken up. Macduff asks if the King has woken yet: Duncan had asked to see Macduff early that morning. Macbeth points out where Duncan is sleeping, and Macduff goes off to wake him. As they wait for Macduff to return, Lennox describes the terrible storm that raged the previous night and sounded like "strange screams of death" (2.3.52). Macduff cries out in horror and runs onstage. Macbeth and Lennox ask what happened, then run to Duncan\'s chamber. Banquo, Malcolm, and Donalbain wake. Lady Macbeth enters, pretending not to know what happened, and expressing horror when Macduff tells her of the murder. Macbeth returns, and wishes he had died rather than have to see such a thing. Malcolm and Donalbain enter and ask what\'s happened. Lennox tells them that Duncan was murdered by his drunken attendants. Macbeth wishes aloud that he hadn\'t killed the attendants. When Macduff asks why Macbeth did kill the attendants, Macbeth says he was so furious that they had murdered the Duncan that he couldn\'t control himself. Lady Macbeth faints. The thanes agree to meet in the hall to discuss what\'s happened. Malcolm and Donalbain, though, remain behind. They realize that one of the thanes is probably the murderer and fear that they\'ll be the next targets. They decide to flee: Malcolm to England and Donalbain to Ireland. '},
                {number: '4', text: 'Ross and an old man stand near Macbeth\'s castle. They discuss the unnatural portents just before and after Duncan\'s murder: darkness during the day, owls killing hawks, horses eating one another. Macduff enters. He says it seems Duncan\'s attendants did commit the murder, and that because Malcolm and Donalbain fled they likely were behind the plot. Macduff then says Macbeth has been made king, and that he has already gone to Scone for the coronation. Ross heads to the coronation. But Macduff returns to his own castle at Fife. '}
            ],
            themecards: [
                {
                    name: 'Guilt',
                    definition: 'Macbeth speaks this line when he encounters his wife right after murdering Duncan. He refers to both the literal blood on his hand but also to his sense of guilt. He uses grand and dramatic language to imply that the blood could stain all the worlds oceans red. His language implies that the consequences of his action will not be easily hidden, even though his wife implies that blood can be simply washed away. He will forever be a changed man as a result of what he has done. Interestingly, later in the play, Lady Macbeth will also hallucinate that she has blood on her hands and is unable to get them clean, symbolizing her sense of guilt.',
                    quote: 'Will all great Neptunes ocean wash this blood / ' +
                        'Clean from my hand? No, this my hand will rather / ' +
                        'The multitudinous seas incarnadine (2.2)'},
            ],
            quotecards: [
                //in quote remember about <br/>
                {
                    quote: 'Where we are, / Theres daggers in mens smiles, / The near in blood, the nearer bloody.',
                    location: 'Lines 139-141, Act II, Sc. III',
                    character: 'Donalbain',
                    explanation: 'Donalbain speaks these lines to Malcolm, his brother. They come to know, King Duncan, their father, has been murdered. Following the murder of the King, Macbeth also kills the guards in rage. As they try to recover from their loss, they understand that they cannot trust anyone. Also, people can pretend to be friends while harboring hate in their heart, and enemies are usually closer than their friends. The last line shows Donalbains mistrust on people around them. They know that their own relatives are now after their lives to get the throne of their father.'},
                {
                    quote: 'Methought I heard a voice cry \'Sleep no more! / ' +
                        'Macbeth does murder sleep.\' / ' +

                        '[]' +

                        'What hands are here? ha! they pluck out mine eyes. / ' +

                        'Will all great Neptune\'s ocean wash this blood / ' +

                        'Clean from my hand? No, this my hand will rather / ' +

                        'The multitudinous seas in incarnadine, / ' +

                        'Making the green one red.',
                    location: 'Act II, Scene 2',
                    character: 'Macbeth',
                    explanation:
                        'Macbeth speaks these lines immediately after murdering the king. To "murder sleep" has a double meaning. Macbeth has killed a sleeping man, and he\'s also killed his own serenity. Macbeth knows that because of this action, he will never be able to rest peacefully. \n' +
                        '\n' +
                        'The guilt Macbeth feels stirs hallucinations and gruesome visions of blood. He\'s shocked by the sight of his murderous hands. ("They pluck out mine eyes.") In his tormented mind, his hands are soaked with so much blood, they would turn the ocean red. \n' +
                        '\n' +
                        'Lady Macbeth shares Macbeth\'s crime, but does not immediately show guilt. She coldly returns the daggers to the crime scene and smears blood on the king\'s sleeping grooms so that they will be blamed. Seemly unruffled, she tells her husband, "A little water clears us of this deed" (Act II, Scene 2).\n'},
                {
                    quote: 'If I had died but an hour before this chance, / I had lived a blessed time; for, from this instant, / Theres nothing serious in mortality: / All is but toys: renown and grace is dead; / The wine of life is drawn, and the mere lees / Is left this vault to brag of ',
                    location: 'Act II, scene III',
                    character: 'Macbeth',
                    explanation: 'Enjoy this fine example of verbal irony: the hearers assume Macbeths lamentation is caused by the death of the king; Macbeth actually speaks of his murdering of the king. In this passage, Macbeth expresses his guilt over what he has done, a guilt which he sheds as the play progresses and Macbeth orders the murders of Banquo and Macduffs family.'},
            ]
        })
    }
</script>

