                                    <h2>William Shakespeare</h2>
                                    <p>
                                        William Shakespeare was an English poet, playwright, and actor. He was born on 26 April 1564 in Stratford-upon-Avon. His father was a successful local businessman and his mother was the daughter of a landowner. Shakespeare is widely regarded as the greatest writer in the English language and the world's pre-eminent dramatist. He wrote about 38 plays, 154 sonnets, two long narrative poems, and a few other verses. His plays have been translated into every major living language and are performed more often than those of any other playwright.
                                    </p>
                                    <h4>
                                        Marriage and career
                                    </h4>
                                    <p>
                                        Shakespeare married Anne Hathaway at the age of 18. She was eight years older than him. They had three children: Susanna, and twins Hamnet and Judith. Between 1585 and 1592, he began a successful career in London as an actor, writer, and part-owner of a playing company called the Lord Chamberlain's Men, later known as the King's Men.
                                    </p>
                                    <h4>
                                        Retirement and death
                                    </h4>
                                    <p>
                                        Around 1613, at the age of 49, he retired to Stratford. He died on the 23 April 1616, at the age of 52. In his will, Shakespeare left the bulk of his large estate to his elder daughter Susanna.
                                    </p>
                                    <h4>
                                        His work
                                    </h4>
                                    <p>
                                        Shakespeare produced most of his known work between 1589 and 1613. His early plays were mainly comedies and histories and these works remain regarded as some of the best work produced in these genres. He then wrote mainly tragedies until about 1608, including Hamlet, Othello, King Lear, and Macbeth, considered some of the finest works in the English language. In his last phase, he wrote tragicomedies, also known as romances, and collaborated with other playwrights.

                                        Shakespeare's plays remain highly popular today and are constantly studied, performed, and reinterpreted in diverse cultural and political contexts throughout the world.
                                    </p>
                                    <h2>Macbeth</h2>
                                    <p>
                                        The Tragedy of Macbeth (commonly called Macbeth) is a play by William Shakespeare about a regicide and its aftermath. It is Shakespeare's shortest tragedy and is believed to have been written sometime between 1603 and 1607. Shakespeare's source for the tragedy are the accounts of King Macbeth of Scotland, Macduff, and Duncan in Holinshed's Chronicles (1587), a history of England, Scotland and Ireland familiar to Shakespeare and his contemporaries. However, the story of Macbeth as told by Shakespeare bears little relation to real events in Scottish history, as Macbeth was an admired and able monarch.
                                    </p>
