<template>
    <v-app>

        <v-tabs
                background-color="grey lighten-2"
                center-active
                fixed-tabs
        >
            <v-tab>
                Summaries
            </v-tab>

            <v-tab-item>
                <v-expansion-panels
                        multiple
                        popout
                >
                    <v-expansion-panel :key="panel.text" v-for="panel in panels">
                        <v-expansion-panel-header>
                            <h3 class="text-center">Scene {{panel.number}}</h3>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-card>
                                <v-card-text>
                                    {{panel.text}}
                                </v-card-text>
                            </v-card>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-tab-item>

            <v-tab>
                Themes
            </v-tab>

            <v-tab-item>
                <v-card
                        :key="themecard.name"
                        class="mx-auto"
                        shaped
                        v-for="themecard in themecards"
                >
                    <v-card-title>{{themecard.name}}</v-card-title>
                    <v-expansion-panels
                            multiple
                            popout
                    >
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                Quote
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{themecard.quote}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                        <v-expansion-panel >
                            <v-expansion-panel-header>
                                Explanation
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{themecard.definition}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-card>
            </v-tab-item>

            <v-tab>
                Quotes
            </v-tab>

            <v-tab-item>
                <v-card
                        :key="quotecard.quote"
                        class="mx-auto"
                        shaped
                        v-for="quotecard in quotecards"
                >
                    <v-card-title>
                        {{quotecard.quote}}
                    </v-card-title>
                    <v-card-subtitle>
                        Location: {{quotecard.location}}
                    </v-card-subtitle>
                    <v-card-text>
                        Character: {{quotecard.character}}
                    </v-card-text>
                    <v-expansion-panels
                            multiple
                            popout
                    >
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                Explanation
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{quotecard.explanation}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-card>
            </v-tab-item>
        </v-tabs>
    </v-app>
</template>

<script>
    export default {
        name: "Act5",
        data: () => ({
            panels: [
                {number: '1', text: 'It is night in Macbeth\'s castle of Dunsinane. A doctor and a gentlewoman wait. The gentlewoman called the doctor because she has seen Lady Macbeth sleepwalking the last few nights, but she refuses to say what Lady Macbeth says or does. Lady Macbeth enters, holding a candle, but asleep. Lady Macbeth keeps rubbing her hands as if to wash them while saying "out, damned spot" (5.1.30). Then Lady Macbeth seems to relive her attempt to convince Macbeth to kill Duncan, concluding with the words: "Yet who would have thought the old man to have had so much blood in him" (5.1.33-34)? The horrified doctor and gentlewoman watch as Lady Macbeth then relives conversations with Macbeth after the murder of Banquo and hears an imaginary knocking and rushes off to bed. The doctor says the disease is beyond his power to cure, and that "unnatural deeds do breed unnatural troubles" (5.1.61-62). He also says he dares not speak about what he\'s just witnessed. '},
                {number: '2', text: 'Lennox and other Scottish lords and soldiers discuss the situation: Malcolm and his army are at Birnam Wood. Macbeth, in a constant rage verging on madness, is fortifying the stronghold of Dunsinane. The lords agree that Macbeth is tormented by his terrible actions, and that those who follow him do so out of fear, not love. The lords ride to join Malcolm. '},
                {number: '3', text: 'Macbeth dismisses all reports about Malcolm\'s army, saying he\'ll fear nothing until Birnam Wood marches to Dunsinane and mocking Malcolm as a man born of woman. He shouts for his servant Seyton to bring his armor, then muses how sick at heart he feels, how withered his life has become. He asks the doctor about Lady Macbeth, then commands that the man cure her. In an aside, the doctor says that if he could escape Dunsinane, no fee of any size could bring him back. '},
                {number: '4', text: 'In Birnam Wood, Malcolm walks with Macduff, Siward, Young Siward, and others Scottish and English lords. Malcolm gives orders that to hide the size of their army, all soldiers should cut a branch from a tree and hold it upright as they march. '},
                {number: '5', text: 'Macbeth laughs at the coming army, but seems bored by his lack of fear. Suddenly, a woman cries out. Seyton investigates, and returns with news that Lady Macbeth has died. Macbeth gives a speech about life: "Tomorrow, and tomorrow, and tomorrow / Creeps in this petty pace from day to day," concluding that life "is a tale / told by an idiot, full of sound and fury, / signifying nothing" (5.5.18-27). A servant rushes in with news that Birnam Wood is marching toward Dunsinane. Macbeth rushes to see for himself, and realizes the witches tricked him. He feels fear for the first time, calls to raise the alarm, and says that at least he\'ll die fighting. '},
                {number: '6', text: 'Malcolm orders his men to throw down the branches they carry. The first charge against Dunsinane commences under Siward and Macduff. '},
                {number: '7', text: 'In the fighting, Macbeth encounters and fights Young Siward. Though Young Siward is brave, Macbeth quickly kills him and says in a mocking tone that he fears no man of woman born. '},
                {number: '8', text: 'Macduff searches for Macbeth, vowing to kill him to avenge his family. '},
                {number: '9', text: 'Malcolm and Siward meet. They have easily captured the castle because Macbeth\'s men barely fight back. '},
                {number: '10', text: 'Macbeth and Macduff meet. Macbeth says he has avoided fighting Macduff because he has too much blood on his hands already. They fight. Macbeth mocks Macduff, saying his effort is wasted: no one of woman born can beat Macbeth. But Macduff replies that he was "untimely ripped" from his mother\'s womb" (5.10.16). Macbeth, suddenly fearful now that the prophecy has turned against him, refuses to fight him. But Macduff calls Macbeth a coward and says that Macbeth will be mocked across Scotland if he surrenders. Despite certain death, Macbeth attacks. Macduff kills him. '},
                {number: '11', text: 'Malcolm, Siward, Ross, and others enter. Ross tells Siward of Young Siward\'s death. Siward asks if his son died from wounds on the front or back. Ross replies the front. Siward is content, denying Malcolm\'s comment that his son is worth more mourning than that. Macduff enters, carrying Macbeth\'s severed head. He proclaims Malcolm to be King of Scotland and swears his loyalty. Malcolm accepts the thanes\' loyalty and makes them all earls (a higher rank). He pledges to "plant" a new peace, and to heal the wounds Macbeth and his "fiend-like queen" (5.11.35) inflicted on Scotland. '},
            ],
            themecards: [
                {
                    name: 'Guilt',
                    definition: 'Lady Macbeth speaks these lines after she has gone mad. They are the final words she utters in the play, and they reveal how guilt has crushed her strong and assertive personality. She now has to be cared for like a child, and has no plans for the future. No matter how much she repents, the violence and death cannot be undone.',
                    quote: 'To bed, to bed. Theres knocking at the gate. Come, / ' +
                        'come, come, come, give me your hand. Whats done / ' +
                        'cannot be undone. To bed, to bed, to bed. (5.1)'},
                {
                    name: 'Children',
                    definition: 'Siward, a minor character, speaks these lines at the end of the play when he learns that his son has been killed in battle. Even though he is grief-stricken, he is proud that his son died a brave and honorable death. The line shows how important honor and valor are to characters in the play, and also how important it was for Macbeth to be defeated. A father can consider it worthwhile to have lost his child if it means knowing that Macbeth no longer occupies the throne.',
                    quote: 'Had I as many sons as I have hairs (new line) ' +
                        'I would not wish them a fairer death (5.11)'},
            ],
            quotecards: [
                //in quote remember about <br/>
                {
                    quote: 'Heres the smell of blood. / All the perfumes of Arabia will not sweeten this little hand.',
                    location: 'Lines, 144-145, Act V, Sc. I',
                    character: 'Lady Macbeth',
                    explanation: 'Lady Macbeth speaks these words as she begins to feel guilty after King Duncans death. While Macbeth is able to accept his crime and becomes ambitious, Lady Macbeth loses her grip over the situations and becomes weaker. Also, remorse begins to weigh heavily upon her. Being a weak-minded, she starts sleepwalking and suffers from acute depression. As she recalls Kings death realizes that she played a huge part in the murder, she will not be able to wash away or hide her crime.'},
                {
                    quote: 'Out, out, brief candle! / Lifes but a walking shadow, a poor player / That struts and frets his hour upon the stage / And then is heard no more. It is a tale / Told by an idiot, full of sound and fury, / Signifying nothing.',
                    location: 'Lines 26-31, Act V, Scene V',
                    character: 'Macbeth',
                    explanation: 'Macbeth speaks these words after Lady Macbeths death. He expresses that human life is nothing but a walking shadow. He also compares life with a candle that burns brightly before it melts away. He realizes that because of his actions he will face terrible consequences and his demise is inevitable. He further explains that life is nothing and humans are mere actors on the stage. Each has a small part to play before they become insignificant.'},
                {
                    quote: 'Despair thy charm, / ' +
                        'And let the angel whom thou still hast served / ' +
                        'Tell thee, Macduff was from his mothers womb / ' +
                        'Untimely ripped.',
                    location: 'Act 5, Scene 8',
                    character: 'Macduff',
                    explanation: 'Macbeth has already learned that Birnam Wood did move, as the Witches predicted. Here, he learns from Macduff that the Witches last prediction has come true as well. As Macduff tells him in Act 5, Scene 8, Macduff was taken from his mothers womb in a Caesarian section and thus was not born. Macbeth is doomed.'},
            ]
        })
    }
</script>

<style scoped>

