<template>
    <v-app>

        <v-tabs
                background-color="grey lighten-2"
                center-active
                fixed-tabs
        >
            <v-tab>
                Summaries
            </v-tab>

            <v-tab-item>
                <v-expansion-panels
                        multiple
                        popout
                >
                    <v-expansion-panel :key="panel.text" v-for="panel in panels">
                        <v-expansion-panel-header>
                            <h3 class="text-center">Scene {{panel.number}}</h3>
                        </v-expansion-panel-header>
                        <v-expansion-panel-content>
                            <v-card>
                                <v-card-text>
                                    {{panel.text}}
                                </v-card-text>
                            </v-card>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-tab-item>

            <v-tab>
                Themes
            </v-tab>

            <v-tab-item>
                <v-card
                        :key="themecard.name"
                        class="mx-auto"
                        shaped
                        v-for="themecard in themecards"
                >
                    <v-card-title>{{themecard.name}}</v-card-title>
                    <v-expansion-panels
                            multiple
                            popout
                    >
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                Quote
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{themecard.quote}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                        <v-expansion-panel >
                            <v-expansion-panel-header>
                                Explanation
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{themecard.definition}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-card>
            </v-tab-item>

            <v-tab>
                Quotes
            </v-tab>

            <v-tab-item>
                <v-card
                        :key="quotecard.quote"
                        class="mx-auto"
                        shaped
                        v-for="quotecard in quotecards"
                >
                    <v-card-title>
                        {{quotecard.quote}}
                    </v-card-title>
                    <v-card-subtitle>
                        Location: {{quotecard.location}}
                    </v-card-subtitle>
                    <v-card-text>
                        Character: {{quotecard.character}}
                    </v-card-text>
                    <v-expansion-panels
                            multiple
                            popout
                    >
                        <v-expansion-panel>
                            <v-expansion-panel-header>
                                Explanation
                            </v-expansion-panel-header>
                            <v-expansion-panel-content>
                                <v-card shaped>
                                    <v-card-text>
                                        {{quotecard.explanation}}
                                    </v-card-text>
                                </v-card>
                            </v-expansion-panel-content>
                        </v-expansion-panel>
                    </v-expansion-panels>
                </v-card>
            </v-tab-item>
        </v-tabs>
    </v-app>
</template>

<script>
    export default {
        name: "Act3",
        data: () => ({
            panels: [
                {number: '1', text: 'In the royal palace of Forres, Banquo states his suspicion that Macbeth fulfilled the witches\' prophecy by foul play. But he notes that since the prophecy came true for Macbeth, perhaps it will come true for him as well. Macbeth enters, with other thanes and Lady Macbeth. He asks Banquo to attend a feast that evening. Banquo says he will, but that meanwhile he has to ride somewhere on business. Macbeth asks if Fleance will be riding with him. Banquo says yes, then departs. Once he\'s alone, Macbeth sends a servant to summon two men. As he waits for them to arrive, he muses if the witches prophecy is true, then Banquo\'s descendants will be king, and he\'ll have murdered Duncan for nothing. The two men (identified in the stage directions as "murderers") enter. Macbeth tells them it\'s Banquo\'s fault they\'re poor, then questions their manhood for bearing such offenses. The murderers agree to kill Banquo and Fleance. '},
                {number: '2', text: 'After sending a servant to fetch Macbeth, Lady Macbeth, waits, and muses that she has what she desires but isn\'t happy. Macbeth enters. She asks why he spends so much time alone. Macbeth responds: "We have scorched the snake, not killed it" (3.2.15). He fears someone might try to kill him as he killed Duncan, and seems envious of Duncan\'s "sleep" (3.2.25). Lady Macbeth reminds him to be "bright and jovial" at the feast. Macbeth tells her to act the same. But then Macbeth moans, "O, full of scorpions is my mind, dear wife!" (3.2.37) because Banquo and Fleance are still alive. Macbeth says that before the night is through there shall be a "deed of dreadful note" (3.2.45), but adds that she\'s better off being innocent until she can applaud what has happened. '},
                {number: '3', text: 'The two murderers lie in wait a mile from the royal castle. A third murderer joins them, sent by Macbeth. Banquo and Fleance enter. The murderers attack. Banquo is killed, but Fleance escapes. The murderers return to the castle to tell Macbeth what\'s happened. '},
                {number: '4', text: 'Macbeth bids all the lords welcome to the feast. Just at that moment, he notices that one of the murderers is standing at the door. The murderer tells Macbeth that Banquo is dead but Fleance escaped. Macbeth comforts himself that Fleance will not be a threat for quite some time. Lady Macbeth calls to Macbeth and asks him to return to the feast and sit. But Macbeth doesn\'t see an empty seat at the table. When Lennox gestures at a seat, saying it\'s empty, Macbeth sees Banquo\'s ghost sitting there. Macbeth alone can see the ghost. He astonishes the thanes by shouting at the empty chair. Lady Macbeth tells the thanes not to worry, that since childhood Macbeth has suffered fits. She pulls Macbeth aside and once again questions his manhood. The ghost disappears. Macbeth rambles about murders and spirits risen from the grave until Lady Macbeth reminds him of his guests. He echoes her story about his fits, then leads a toast to the missing Banquo. The ghost reappears and Macbeth, terrified, starts shouting at it. Lady Macbeth tries to play down her husband\'s strange behavior. The ghost again disappears. Macbeth is amazed that everyone could be so calm in the face of such sights. When Ross asks what sights, Lady Macbeth steps in and asks the guests to leave at once. The thanes exit. Macbeth tells Lady Macbeth: "Blood will have blood" (3.4.121), and asks what Lady Macbeth makes of the fact that Macduff does not appear at the royal court. He decides to visit the weird sisters to find out more about his fate. He says: "I am in blood / Stepped in so far" (3.4.135) that turning back is as difficult as continuing on. '},
                {number: '5', text: 'The weird sisters meet with Hecate, the goddess of witches. She rebukes the sisters for meddling with Macbeth without first consulting her. But she says she\'ll help them when Macbeth comes to see them tomorrow. She says that they\'ll show him visions that will give him confidence and "draw him to his confusion" (3.5.29). '},
                {number: '6', text: 'Lennox and another lord talk sarcastically about Macbeth and the too great similarities between the murders of Duncan and Banquo, with Donalbain and Malcolm accused of the first and Fleance blamed for the second. Macduff, the lord says, has gone to England to meet with Malcolm and try to get the English King Edward and his lords to gather an army to help them defeat Macbeth. The rumor is that Macbeth sent a messenger to Macduff. Macduff rebuffed the messenger, who turned his back as if to say that Macduff would pay for that decision. Both men hope Macduff remains safe and soon returns with the armies of Malcolm and England to free Scotland from Macbeth. '}
            ],
            themecards: [
                {
                    name: 'Ambition',
                    definition: 'Macbeth speaks this line after he has become king, but continues to feel restless and insecure. He is afraid that he might lose his position and is also frustrated by the fact that he has no heir. Without the knowledge that his lineage will continue after him, Macbeth finds it meaningless to be king. This quote reveals how him giving in to his ambition and murdering Duncan has not brought him peace, but rather has just left him more paranoid and anxious. The line also reveals how Macbeths first violent action sets off a chain reaction of him continuing to commit violent actions in order to maintain his hold on the power he has gained.',
                    quote: 'To be thus is nothing, but to be safely thus (3.1)'},
                {
                    name: 'Guilt',
                    definition: 'Macbeth speaks this line when Banquos ghost appears to him at the banquet. Macbeths vision of the ghost reveals his guilt over ordering the murder of Banquo and his young son. His sense of guilt is so powerful that he loses his sense of reality and cannot be sure whether he is having a vision or not. He speaks these lines in order to try and reassure himself that Banquo is truly dead. In doing so, Macbeth reveals that his tormented consciousness is leading him to start losing his grip on sanity.',
                    quote: 'Thy bones are marrowless, thy blood is cold / '  +
                        'Thou hast no speculation in those eyes (3.4)'},
                {
                    name: 'Children',
                    definition: 'Macbeth speaks these lines when he is brooding about his worries that, as the witches have prophesied, Banquos heirs will someday gain control of the Scottish crown. Macbeth reveals that he feels guilty about the terrible things he has done, and that he is wondering if these acts were worthwhile. Even though he currently holds power, Macbeth lacks children who will hold power after him and this makes him fear that he has committed terrible deeds for no reason. These lines speak to a strong interest in stable succession, which was a key political issue in Shakespeares time.',
                    quote: 'For Banquos issue have I filed my mind / For them the gracious Duncan have I murdered (3.1)'},
            ],

            quotecards: [
                //in quote remember about <br/>
                {
                    quote: 'Things without all remedy / Should be without regard: Whats done is done.',
                    location: 'Lines 10-12, Act III, Scene II',
                    character: 'Lady Macbeth',
                    explanation: 'Lady Macbeth speaks these words to her husband, Macbeth, after he kills King Duncan. Macbeth suffers from hallucinations and mental breakdown after the murder. Hence, Lady Macbeth tells that there is no point of regretting over things that already happened because you cannot reverse it. Lady Macbeth encourages her husband to remove any regrets and forget the crime.'},
                {
                    quote: 'It will have blood, they say. Blood will have blood. / ' +
                        'Stones have been known to move, and trees to speak. / ' +
                        'Augurs and understood relations have / ' +
                        'By maggot pies and choughs and rooks brought forth / ' +
                        'The secretst man of blood.',
                    location: 'Act 3, Scene 4',
                    character: 'Macbeth',
                    explanation: 'By the time Macbeth says these lines, in Act 3, Scene 4, Banquo has been murdered at Macbeths command. Macbeth has been haunted by Banquos ghost at what was supposed to be a celebratory dinner. The full horror of what he has done in order to achieve his ambition is now clear to Macbeth, and he warns his wife that the dead will have their revenge.'},
                {
                    quote: 'O treachery! Fly, good Fleance, fly, fly, fly! / ' +
                        'Thou mayst revenge  O slave!',
                    location: 'Act3, Scene 3',
                    character: 'Banquo',
                    explanation: 'These lines are Banquos dying words, as he is slaughtered by the murderers Macbeth has hired in Act 3, scene 3. In his dying breaths, Banquo urges his son, Fleance, to flee to safety, and charges him to someday revenge his fathers death. This sets the stage how the play will end, when Macbeth realizes that the Witches prophecy will come true, and Banquos children will rule Scotland.'},
            ]
        })
    }
</script>

